<script lang="coffeescript">var closeMenu, debouncedMenu, dispatch, getProjects, goToCollections, goToProject, list, menuButtonRef, openMenu, resource, showMenu, sort, width;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  push
} from 'svelte-spa-router';

import Menu from "../../../components/Overlay/Menu/ComposedMenuFavorites.svelte";

import Project from "../../../helpers/project.js";

resource = new Project();

list = [];

getProjects = function() {
  var scb;
  scb = function(projects) {
    return list = projects;
  };
  return resource.select(scb, null, null);
};

getProjects();

export var item = void 0;

menuButtonRef = void 0;

showMenu = false;

goToCollections = function() {
  return push("#/projects");
};

goToProject = function(item) {
  showMenu = false;
  return push(`#/projects/${item.id}`);
};

openMenu = function() {
  return showMenu = true;
};

closeMenu = function() {
  return showMenu = false;
};

debouncedMenu = openMenu.debounce(500);

width = "200px";

sort = function(list) {
  if (list != null) {
    return list.sortBy("ordinal");
  } else {
    return [];
  }
};

$: sortedlist = sort(list);
</script>
