<span>
<a href="#/projects"  bind:this={menuButtonRef} on:mouseover={debouncedMenu}><em>Collections</em></a>
<Menu trigger={menuButtonRef} bind:showMenu={showMenu} align="left" {width} matchTrigger={true}>
  <ul>

    <li>
      <button type="button" on:click={goToCollections}>Collections</button>
    </li>

    {#if list}
    <hr/>
    {#each sortedlist as item, index}
    <li>
      <button type="button" on:click={goToProject(item)} ellipsis><span ellipsis>{item.name}</span></button>
