<script lang="coffeescript">var add, destroy, dispatch, dropEvent, goToItem, hasAssignments, itemUrl, showComments, sort, style, subDocument, update, updateAttribute, updateProject;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  push,
  pop,
  replace,
  location
} from 'svelte-spa-router';

import Indent from "../Indent.svelte";

import DraggableItem from "../Lists/Nestable/DraggableIndentableItem.svelte";

import DroppableDivider from "../Lists/Nestable/DroppableDivider.svelte";

import QuickEntry from "../QuickEntry/GenericItem.svelte";

import Menu from "../Menus/Project.svelte";

import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte';

import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte';

import PageHeader from '../PageHeader.svelte';

import ProgressPopover from '../Popovers/Progress.svelte';

import DueDatePopover from '../Popovers/DueDate.svelte';

import EstimatePopover from '../Popovers/Estimate.svelte';

import Section from "./_Section.svelte";

export var assignments = [];

// optional
export var collection = "tasks";

import Project from "../../../helpers/project.js";

import Assignment from "../../../helpers/assignment.js";

export var list = void 0;

export var item = void 0;

itemUrl = "#/projects/";

subDocument = new Project;

showComments = function(item) {
  if (item.comments != null) {
    return item.comments.length > 0;
  } else {
    return false;
  }
};

goToItem = function(item) {
  return push(`/projects/${item.id}`);
};

updateProject = function(p) {
  list = list; // refresh array
  console.log(p);
  return dispatch("update", p);
};

updateAttribute = function(e) {
  var change, changedItem;
  console.log("ListItme.svelte updateAttribute");
  console.log(e.detail);
  change = e.detail;
  // change["id"] = item.id
  changedItem = list.find({
    id: change.id
  });
  Object.merge(changedItem, change);
  list = list; // refresh array
  console.log(change);
  return dispatch("update", change);
};

add = function(e) {
  return dispatch("add", e.detail);
};

hasAssignments = function() {
  var output;
  output = false;
  if (item.assignments != null) {
    output = item.assignments.length > 0;
  }
  return output;
};

update = function() {
  return dispatch("update");
};

destroy = function(e) {
  return dispatch("destroy", e.detail);
};

sort = function(list) {
  if (list != null) {
    return list.sortBy("ordinal");
  } else {
    return [];
  }
};

export var listId = uuid();

export var sameListOnly = true;

dropEvent = function(e) {
  var dragged, dropped, refresh;
  refresh = true;
  if (e.detail.divider != null) {
    console.log("shift ordinals");
    // up or down?
    if (e.detail.dragIndex !== e.detail.dropIndex) {
      if (e.detail.dropIndex < e.detail.dragIndex) {
        list.forEach(function(item) {
          if (item.ordinal >= e.detail.dropIndex) {
            return item.ordinal = item.ordinal + 1;
          }
        });
        dragged = list.find({
          id: e.detail.dragItem.id
        });
        dragged.ordinal = e.detail.dropIndex;
      } else {
        list.forEach(function(item) {
          if (item.ordinal <= e.detail.dropIndex) {
            return item.ordinal = item.ordinal - 1;
          }
        });
        dragged = list.find({
          id: e.detail.dragItem.id
        });
        dragged.ordinal = e.detail.dropIndex - 1;
      }
    } else {
      refresh = false;
    }
  } else {
    dragged = list.find({
      id: e.detail.dragItem.id
    });
    dropped = list.find({
      id: e.detail.dropItem.id
    });
    dragged.ordinal = e.detail.dropItem.ordinal;
    dropped.ordinal = e.detail.dragItem.ordinal;
  }
  // cleanup ordinals in case there are stragglers
  list.sortBy("ordinal");
  return list.forEach(function(item, index) {
    item.ordinal = index;
    return dispatch("update", item);
  });
};

style = ":hover {font-weight: bold}";

add = function(event) {
  var newRecord, scb;
  newRecord = subDocument.stub(event.detail);
  scb = function(inserted) {
    console.log(inserted);
    return push(`/projects/${inserted.id}`);
  };
  newRecord.ordinal = (list || []).length;
  return subDocument.insert(newRecord, scb, null, null);
};

$: sortedlist = sort(list);
</script>
<style lang="sass">[type="list-item"] {
  width: 100%;
  max-width: 100%; }
  [type="list-item"] [present] {
    display: none; }
  [type="list-item"] [present="true"] {
    display: flex; }
  [type="list-item"]:hover [present] {
    display: flex; }

header h1 {
  font-size: 36px;
  color: var(--darkest-blue); }

section {
  margin: 0px 56px; }

/*# sourceMappingURL=ProjectList.svelte.css.map */</style>

  <PageHeader>Collections</PageHeader>
  <section>
    {#if list && list.length}
      <div>
        <DroppableDivider index={0} {listId} {sameListOnly} on:drop={dropEvent} />
        {#each sortedlist as item, index}
          <DraggableItem item={item} index={index} {listId} {sameListOnly} on:drop={dropEvent} on:update={updateAttribute}>
            <div layout="row" type="list-item">
              <Indent level={item.indent}/>

              <div flex="initial" section="progress">
                <ProgressPopover {item} bind:value={item.progress} on:update={updateAttribute} on:open on:close/>
              </div>

              <div flex ellipsis>
                <InlineInput bind:value={item.name} on:update={updateProject(item)} on:clicked={goToItem(item)} {style} let:label>
                  <span ellipsis><a href="{itemUrl}{item.id}" on:click|preventDefault><em>{label || "No Name"}</em></a></span>
                </InlineInput>
              </div>
              <div flex="initial">
                <div layout="row">



                  <!-- due date menu -->
                  <div flex="initial" present={item.due_date != null}>
                    <DueDatePopover {item} value={item.due_date} on:update={updateAttribute} on:open on:close/>
                  </div>
                  <div flex="initial" present={false}>
                    <EstimatePopover {item} value={item.due_date} on:update={updateAttribute} on:open on:close/>
                  </div>



                </div>
              </div>

            </div>

            <div slot="menu">
              <Menu {item} on:update={updateAttribute} on:destroy={destroy}/>

            </div>
          </DraggableItem>
          <DroppableDivider {item} index={index + 1} {listId} {sameListOnly} on:drop={dropEvent} />
        {/each}
      </div>
    {/if}
    <QuickEntry on:add={add} stubPlaceholder="Add Project" />
  </section>
