<script lang="coffeescript">var assign, assignmentClick, cancel, clear, clearAssignment, dispatch, email, itemLabelString, markComplete, newValue, progressMenuClick, save, show, sort, state, triggerClick, triggerRef;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

export var item = void 0;

export var value = void 0;

newValue = void 0;

import Popover from '../../../components/Overlay/Popover/Popover.svelte';

import Menu from "../../../components/Overlay/Menu/ComposedMenuFavorites.svelte";

import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte';

import Gravatar from '../../../components/External/Gravatar/Gravatar.svelte';

export var project = void 0;

// menu
show = false;

triggerClick = function() {
  console.log("triggerClick assignment");
  dispatch("open");
  return show = true;
};

triggerRef = void 0;

progressMenuClick = function(e) {
  return console.log("progressmenuclick");
};

cancel = function() {
  show = false;
  dispatch("close");
  return newValue = value;
};

save = function() {
  show = false;
  dispatch("close");
  return dispatch("save", {
    id: item.id,
    assigned_to_id: newValue
  });
};

markComplete = function() {
  return newValue = 100;
};

clear = function() {
  return newValue = null;
};

if (!item.assigned_to_id) {
  state = "empty";
} else {
  state = "green";
}

$: newValue = value || null;

email = function(val) {
  var output;
  output = "unknown";
  if (item.assigned_to != null) {
    if (item.assigned_to.email != null) {
      output = item.assigned_to.email;
    }
  }
  return output;
};

itemLabelString = function(val) {
  var output;
  output = "Unassigned";
  if (val.assigned_to_id != null) {
    if (["project", "assignment"].includes(val.entity_type)) {
      output = val.assigned_to.full_name;
    } else {
      output = val.assigned_to.name;
      if (val.assigned_to.full_name.length > 0) { // assignment is also assigned to a user id
        output = output + ` (${val.assigned_to.full_name})`;
      }
    }
  }
  return output;
};

assign = function(e) {
  var change;
  if (value !== e.detail) {
    change = {
      id: item.id,
      assigned_to_id: e.detail
    };
    dispatch("save", change);
  } else {
    console.log("didn't save, unchanged assignment id");
  }
  show = false;
  return dispatch("close");
};

assignmentClick = function(assignment) {
  console.log("click");
  return console.log(assignment);
};

clearAssignment = function() {};

// clear it
sort = function(val) {
  if (val != null) {
    return val.sortBy("ordinal");
  } else {
    return [];
  }
};

$: sortedList = sort(project.assignments);

$: isProject = (item.entity_type == "project");

$: gravatar = email(item);
</script>
<style lang="sass">input {
  width: 100px;
  display: inline-block; }

svg[state]:hover .fill {
  fill: var(--gray); }

svg[state] .fill {
  fill: var(--green); }

svg[state="empty"]:hover .fill {
  fill: var(--blue); }

svg[state="empty"] .fill {
  fill: var(--gray); }

span {
  font-weight: inherit; }

ul {
  width: 100%; }
  ul hr {
    border: none;
    border-bottom: 1px solid var(--lightest-gray); }
  ul li {
    min-height: 32px;
    min-width: 200px; }
    ul li div {
      padding: 6px 12px; }
    ul li button {
      display: inline;
      border-radius: 0px;
      width: 100%;
      background: transparent;
      text-align: left; }
      ul li button[indent] {
        border-radius: 4px;
        width: auto;
        font-weight: bold; }
        ul li button[indent]:first-of-type {
          margin-left: 2px; }
      ul li button[disabled] {
        color: var(--light-gray); }
      ul li button:hover {
        background: var(--lightest-gray); }

/*# sourceMappingURL=Assignment.svelte.css.map */</style>

<div>
  <div on:click={triggerClick} bind:this={triggerRef}>
      {#if gravatar == "unknown"}
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" {state}><path fill="none" d="M0 0h24v24H0V0z"/><path d="M12 12c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm0 2c-2.67 0-8 1.34-8 4v2h16v-2c0-2.66-5.33-4-8-4z" class="fill"/></svg>
      {:else}
        <Gravatar value={gravatar}/>
      {/if}
  </div>
  <Menu bind:showMenu={show} on:click={progressMenuClick} bind:trigger={triggerRef}  matchTrigger={false} align="right" top="24px">
    <ul>
      <li><button type="button" on:click={clearAssignment} ellipsis>Unassigned</button></li>
      <hr/>
      {#each sortedList as assignment}
        <li>
          <button type="button" on:click={assignmentClick(assignment)} ellipsis><span ellipsis>{assignment.name}</span></button>
        </li>
      {/each}

    </ul>
  </Menu>
</div>
