<script lang="coffeescript">var add, cancel, clear, dispatch, durationKeyup, effort, markComplete, newTime, newValue, progressMenuClick, save, show, stub, triggerClick, triggerRef, updateEffortMinutes;

import {
  createEventDispatcher,
  onMount
} from "svelte";

dispatch = createEventDispatcher();

export var item = void 0;

export var value = 0;

newValue = void 0;

import Popover from '../../../components/Overlay/Popover/Popover.svelte';

import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte';

import DurationInput from "../../../components/Form/TranslatedInput/DurationInput.svelte";

// menu
show = false;

stub = {
  estimate: 0
};

effort = 0;

onMount(function() {
  return effort = item.estimate / 60;
});

triggerClick = function() {
  dispatch("open");
  return show = true;
};

triggerRef = void 0;

progressMenuClick = function(e) {
  return console.log("progressmenuclick");
};

durationKeyup = function(e) {
  if (e.key === "Escape") {
    return cancel();
  }
};

cancel = function() {
  show = false;
  dispatch("close");
  return newValue = value;
};

save = function() {
  var progressItem;
  show = false;
  progressItem = {
    id: item.id,
    estimate: stub.estimate
  };
  dispatch("close");
  return dispatch("update", progressItem);
};

add = function() {
  var progressItem;
  show = false;
  progressItem = {
    estimated_hours: newValue
  };
  dispatch("close");
  return dispatch("save", progressItem);
};

markComplete = function() {
  return newValue = 100;
};

newTime = 0;

clear = function() {
  return newValue = null;
};

updateEffortMinutes = function(e) {
  return stub.estimate = e.detail.inMinutes;
};

$: newValue = value || 0;
</script>
<style lang="sass">input {
  width: 100px;
  display: inline-block; }

[type="progress"] svg path.fill {
  fill: var(--gray); }

/*# sourceMappingURL=Estimate.svelte.css.map */</style>

<div>

<div on:click={triggerClick} bind:this={triggerRef}>
  <Tooltip>
    <div slot="tip">
      {#if item.estimate == null}<p>No Estimate</p>{/if}
      {#if item.estimate != null}<p>Estimate: {item.estimated_hours} hours</p>{/if}

    </div>
    <div type="progress" id="task-progress-{item.id}">
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M11.99 2C6.47 2 2 6.48 2 12s4.47 10 9.99 10C17.52 22 22 17.52 22 12S17.52 2 11.99 2zM12 20c-4.42 0-8-3.58-8-8s3.58-8 8-8 8 3.58 8 8-3.58 8-8 8zm.5-13H11v6l5.25 3.15.75-1.23-4.5-2.67z" class="fill"/></svg>
    </div>

  </Tooltip>
</div>
<Popover bind:show={show} on:click={progressMenuClick} bind:trigger={triggerRef} align="right" on:close={cancel}>
  <field>
    <label type="field">Estimate</label>
    <DurationInput bind:value={effort} on:change={updateEffortMinutes} autofocus on:keyup={durationKeyup}/>
  </field>
  <div>
    <button type="button" on:click={clear}>Clear</button>
  </div>
  <div>
    <button type="button" on:click={cancel}>Cancel</button>
  </div>
  <div>
    <button type="button" on:click={save}>Save</button>
  </div>

</Popover>
</div>
