<script lang="coffeescript">var changeIndex, checkTrigger, debouncedHide, dispatch, hasfocus, hide, highlightedIndex, menuId, menuItemClick, menuRef, menumouseover, overlayClick, style;

import {
  createEventDispatcher
} from 'svelte';

import {
  slide
} from 'svelte/transition';

dispatch = createEventDispatcher();

export var disabled = false;

export var id = uuid();

export var items = [];

export var width = '200px';

export var top = void 0;

export var selectedIndex = -1;

export var size = void 0;

export var value = "";

export var blankSlate = "";

export var labelKey = "label";

export var idKey = "id";

export var showMenu = false;

export var align = "left";

// trigger is required
export var trigger = void 0;

// if a trigger is defined, it maps the width to the parent
// and is placed just below it, used for combobox and custom select
export var matchTrigger = true;

style = void 0;

menuRef = void 0;

hasfocus = false;

highlightedIndex = -1;

changeIndex = function(direction) {
  var index;
  index = highlightedIndex + direction;
  if (index < 0) {
    index = items.length - 1;
  } else if (index >= items.length) {
    index = 0;
  }
  return highlightedIndex = index;
};

hide = function() {
  return showMenu = false;
};

debouncedHide = hide.debounce(1500);

menuId = uuid();

checkTrigger = function(i) {
  var topPosition;
  //console.log trigger

  //console.log "checktrigger #{align}"
  if (i != null) {
    //console.log i
    //console.log trigger
    //console.log i.offsetLeft
    //console.log i.offsetTop
    if (matchTrigger) {
      style = `width: ${width}; top: ${i.offsetTop + i.offsetHeight}px; left: ${i.offsetLeft + 4}px;`;
    } else {
      console.log("no match trigger");
      if (align === "left") {
        //console.log trigger.offsetParent
        //console.log trigger.offsetParent.clientHeight
        style = `width: ${width};top: ${trigger.offsetParent.clientHeight + 4}px;`;
      } else {
        //console.log style
        console.log("align-right");
        //triggerContainerWidth = trigger.offsetParent.clientWidth
        //triggerCoords = trigger.getBoundingClientRect()
        //right = triggerContainerWidth  - (triggerCoords.width)
        topPosition = top || (i.offsetTop + i.offsetHeight);
        style = `width: ${width};top: ${topPosition}px; right: ${i.offsetLeft + i.offsetWidth}px`;
      }
    }
    console.log("offset parent");
    console.log(i.offsetParent);
  }
  return console.log(style);
};

$: checkTrigger(trigger);

menuItemClick = function(item, index) {
  showMenu = false;
  return dispatch("click", item);
};

overlayClick = function() {
  return showMenu = false;
};

menumouseover = function(e) {
  return debouncedHide.cancel();
};
</script>



<div type="overlay" clear open={showMenu} on:click={overlayClick} on:mouseover={debouncedHide}/>
{#if showMenu}
  <div bind:this={menuRef} menu id={menuId} transition:slide="{{duration: 75}}" {style} {align} on:mouseover={menumouseover} >
    <slot></slot>
  </div>
{/if}
<style lang="sass">div[menu] {
  position: absolute;
  background: white;
  display: inline-block;
  border: 1px solid var(--menu-border);
  width: auto;
  text-align: left;
  z-index: var(--z-index-menu); }
  div[menu][align="left"] {
    left: 0; }

[menu-item] {
  display: block;
  position: relative;
  width: 100%;
  padding: var(--fine-spacing-s) var(--fine-spacing-m); }
  [menu-item]:hover {
    background: var(--lighter-gray); }

/*# sourceMappingURL=ComposedMenuFavorites.svelte.css.map */</style>
